import React, { useState } from "react";
import { useParams } from "react-router-dom";
import { makeStyles, useTheme } from "@material-ui/styles";
import {
  Avatar,
  Container,
  CssBaseline,
  Grid,
  InputAdornment,
  Paper,
  Button,
  Typography,
  Table,
  TableRow,
  TableCell,
  TextField,
  TableBody,
  TableHead
} from "@material-ui/core";
import { red, blue, yellow} from '@material-ui/core/colors'
import TutorialDataService from "services/TutorialService";
import collection from "crud/collections";

const useStyles = makeStyles(
  theme => ({
    paper: {
      marginTop: theme.spacing(2),
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
    },
    avatar: {
      margin: theme.spacing(1),
      backgroundColor: theme.palette.secondary.main,
    }
  })
);

const AddTutorial = () => {
  const { thisCollection}= useParams()
  
  const classes = useStyles();
  const theme = useTheme()
  const dataState ={} 
	 Object.keys(collection[thisCollection].field).map((theField, index)=>{
		  let dataField=collection[thisCollection].field[theField]
		  if (dataField.create===true){
				dataState[theField]=dataField.default
		  }
	  })

  const [tutorial, setTutorial] = useState(dataState);
  const [submitted, setSubmitted] = useState(false);

  const handleInputChange = event => {
	  console.log(event.target)
    const { name, value } = event.target;
    setTutorial({ ...tutorial, [name]: value });
  };

  const saveTutorial = () => {
	  console.log(tutorial,"tutorialsave")
	  
    var data = 
		tutorial
    ;

    TutorialDataService.create(data)
      .then(response => {
        setTutorial(
          response.data
        );
        setSubmitted(true);

      })
      .catch(e => {
        console.log(e);
      });
  };

  const newTutorial = () => {
    setTutorial(dataState);
    setSubmitted(false);
  };

  return (
     <Container component="main" maxWidth="xs">
      <CssBaseline />
    <div className={classes.paper}>
	     <Avatar style={{width: theme.spacing(7), height: theme.spacing(7), backgroundColor: blue[100] }}>
		<Typography variant="h4" style={{color: blue[800]}}>
		  {collection[thisCollection].icon}
		  </Typography>
        </Avatar>
        <Typography component='h1'variant="h5" style={{color: blue[600] }}>{collection[thisCollection].title}</Typography>
        <Grid container spacing={2}>
			{submitted ? (
			
			<Grid item xs={12} sm={12}>
			<div>
			  <Typography  component='h1'variant="h5">
				You submitted successfully!
			  </Typography>
		         <Button color="primary" fullWidth
					variant="contained" onClick={newTutorial}>
					Add
				 </Button>
			</div>
			</Grid>
		  ) : (
		  <React.Fragment>
					{Object.keys(collection[thisCollection].field).map((theField, index)=>{
						let dataField=collection[thisCollection].field[theField]
						if (dataField.create===true){
							return (
							 <Grid item xs={12} sm={12}>
								<TextField
								  type="text"
								  fullWidth variant="outlined"
								  className="form-control"
								  id={theField}
								  label={dataField.show}
								  required
								  defaultValue={dataField.default}
								  onChange={handleInputChange}
								  name={theField}
								  InputProps={{
										startAdornment: (
										<InputAdornment position="start">
											{dataField.icon}
										</InputAdornment>
										),
									}}
								/>	
							</Grid>
						)}
					})}
		         <Button type="submit" color="primary" fullWidth
					variant="contained" onClick={saveTutorial}>
					Submit
				 </Button>
		  </React.Fragment>
      )}
	  </Grid>
    </div>
	</Container>
  );
};

export default AddTutorial;
